<template>
  <v-container :fluid="true">
    <template v-if="selectProductUpdate">
      <UpdateProductDialog
        @handleUpdate="update"
        :product="product"
        :categories="categories"
      />
    </template>
    <v-row>
      <v-col cols="12">
        <h1 class="font-weight-bold mb-3">Все товары</h1>
        <v-breadcrumbs class="px-0 py-0 mb-3" :items="items" divider="/">
          <template v-slot:item="{ item }">
            <v-breadcrumbs-item
              :disabled="item.disabled"
              :exact="true"
              :to="{ name: item.to.name }"
            >
              {{ item.text.toUpperCase() }}
            </v-breadcrumbs-item>
          </template>
        </v-breadcrumbs>
      </v-col>
    </v-row>
    <v-row>
      <v-col cols="12">
        <div class="mb-3">
          <ProductsTable
            :server-items-length="paginate.countElements"
            :products="products"
            :categories="categories"
            @createProduct="addProduct"
            @toggleUpdateProduct="handleUpdateProduct"
            @updatePaginate="handleUpdatePaginate"
          />
        </div>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
import ProductsTable from "../../components/Tables/ProductsTable";
import UpdateProductDialog from "../../components/Dialogs/product/UpdateProductDialog";

export default {
  name: "Products",
  metaInfo: {
    title: "Шестерёнка | Товары"
  },
  data() {
    return {
      filter: {
        page: 1
      },
      items: [
        {
          text: "Панель",
          disabled: false,
          to: { name: "Orders" }
        },
        {
          text: "Все товары",
          disabled: true,
          to: { name: "Products" }
        }
      ],
      selectProductUpdate: null
    };
  },
  methods: {
    ...mapActions({
      loadProducts: "fetchProducts",
      loadProduct: "fetchProduct",
      createProduct: "fetchCreateProduct",
      loadCategories: "fetchCategories",
      updateProduct: "fetchUpdateProduct",
      showDialog: "show"
    }),
    async addProduct(data) {
      await this.createProduct(data);
      await this.loadProducts();
    },
    async handleUpdateProduct(id) {
      await this.loadProduct(id);
      this.selectProductUpdate = id;
      this.showDialog("product:update");
    },
    async handleUpdatePaginate(updatePaginateData) {
      const { page } = updatePaginateData;
      this.filter.page = page;
    },
    async update(formData) {
      await this.updateProduct({ id: formData.id, form: formData });
    }
  },
  computed: {
    ...mapGetters({
      products: "getProducts",
      product: "getProduct",
      paginate: "getProductPaginate",
      categories: "getCategories"
    })
  },
  watch: {
    filter: {
      deep: true,
      async handler() {
        await this.loadProducts(this.filter);
      }
    }
  },
  components: {
    ProductsTable,
    UpdateProductDialog
  },
  async beforeMount() {
    await Promise.all([this.loadProducts(), this.loadCategories()]);
  }
};
</script>
