<template>
  <v-dialog v-model="dialog" width="600">
    <template v-slot:activator="{ on, attrs }">
      <v-btn color="primary lighten-2" dark v-bind="attrs" v-on="on">
        Создать
      </v-btn>
    </template>
    <v-card>
      <v-card-title>
        <span class="text-h5">Создание товара</span>
      </v-card-title>

      <v-card-text>
        <v-container>
          <v-row>
            <v-col cols="12">
              <v-text-field v-model="form.name" label="Название" />
            </v-col>
            <v-col cols="12">
              <v-text-field v-model="form.price" type="number" label="Цена" />
            </v-col>
            <v-col cols="12">
              <v-text-field v-model="form.description" label="Описание" />
            </v-col>
            <v-col cols="12">
              <v-select :items="normalizeSelectData" v-model="select" />
            </v-col>
            <v-col cols="12">
              <v-file-input @change="uploadFile" label="Фотография" />
            </v-col>
            <v-col cols="12">
              <v-checkbox
                v-model="form.is_show"
                :label="form.is_show ? 'Показывать' : 'Скрывать'"
              ></v-checkbox>
            </v-col>
          </v-row>
        </v-container>
      </v-card-text>

      <v-divider></v-divider>

      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn color="primary" @click="create">
          Создать
        </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
export default {
  name: "CreateProductDialog",
  data() {
    return {
      dialog: false,
      select: "",
      form: {
        name: "",
        price: 0,
        description: "",
        is_show: false,
        category: null,
        file: null
      }
    };
  },
  methods: {
    create() {
      this.dialog = false;
      this.$emit("create", this.form);
    },
    uploadFile(file) {
      this.form.file = file;
    }
  },
  computed: {
    normalizeSelectData() {
      return this.categories.map(category => category.name);
    }
  },
  watch: {
    select(name) {
      const category = this.categories.find(category => category.name === name);
      this.form.category = category.id;
    }
  },
  props: {
    categories: {
      type: Array,
      required: true
    }
  },
  beforeMount() {
    if (this.$props.categories.length) {
      this.select = this.$props.categories[0].name;
    }
  }
};
</script>

<style scoped></style>
