<template>
  <v-dialog v-model="$store.state.dialogs.dialogs.product.update" width="600">
    <!-- Modal form update product -->
    <v-card>
      <v-card-title>
        <span class="text-h5">Обновление товара</span>
      </v-card-title>

      <v-card-text>
        <v-container>
          <v-row>
            <v-col cols="12">
              <v-text-field v-model="form.name" label="Название" />
            </v-col>
            <v-col cols="12">
              <v-text-field v-model="form.price" type="number" label="Цена" />
            </v-col>
            <v-col cols="12">
              <v-text-field v-model="form.description" label="Описание" />
            </v-col>
            <v-col cols="12">
              <v-select :items="normalizeSelectData" v-model="select" />
            </v-col>
            <v-col cols="12">
              <v-file-input @change="uploadFile" label="Фотография" />
            </v-col>
            <v-col cols="12">
              <v-img
                style="width: 100% !important;"
                :src="normalizeSourceLink"
                alt=""
              />
            </v-col>
            <v-col cols="12">
              <v-checkbox
                v-model="form.is_show"
                :label="form.is_show ? 'Показывать' : 'Скрывать'"
              ></v-checkbox>
            </v-col>
          </v-row>
        </v-container>
      </v-card-text>

      <v-divider></v-divider>

      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn color="primary" @click="handleUpdate">
          Обновить
        </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
export default {
  name: "UpdateProductDialog",
  data() {
    return {
      select: "",
      form: {
        id: null,
        name: "",
        description: "",
        price: 0,
        category: null,
        file: null
      },
      copy: {}
    };
  },
  methods: {
    handleUpdate() {
      this.$emit("handleUpdate", this.form);
    },
    uploadFile(file) {
      this.form.file = file;
    }
  },
  watch: {
    select(name) {
      const category = this.categories.find(category => category.name === name);
      this.form.category = category.id;
    },
    product: {
      immediate: true,
      handler() {
        this.dialog = true;
        for (let prop in this.product) {
          if (Object.prototype.hasOwnProperty.call(this.form, prop)) {
            this.form[prop] = this.product[prop];
          }
        }

        const category = this.product.categories[0];
        this.select = category.name;
      }
    }
  },
  computed: {
    normalizeSelectData() {
      return this.categories.map(category => category.name);
    },
    normalizeSourceLink() {
      const images = this.product?.images || [];

      // DELETE DUPLICATE CODE
      const sourcePath =
        process.env.VUE_APP_ENV === "production"
          ? process.env.VUE_APP_API_PRODUCTION_PATH
          : process.env.VUE_APP_API_BASE_PATH;

      if (images.length) {
        return `${sourcePath + "/images/" + this.product.images[0].source}`;
      }

      // Not found image
      return "";
    }
  },
  props: {
    product: {
      type: Object,
      required: true
    },
    categories: {
      type: Array,
      required: true
    }
  }
};
</script>
