<template>
  <v-data-table
    v-bind="$attrs"
    :headers="headers"
    :items="products"
    :options="{ page: 1, itemsPerPage: 10 }"
    :page="filter.page"
    v-on:pagination="updatePaginate"
    class="elevation-4"
  >
    <template v-slot:top>
      <v-toolbar flat>
        <v-toolbar-title>Все товары</v-toolbar-title>
        <v-divider class="mx-4" inset vertical></v-divider>
        <v-spacer></v-spacer>
        <!-- Форма добавления нового товара -->
        <CreateProductDialog @create="createProduct" :categories="categories" />
      </v-toolbar>
    </template>

    <template v-slot:item.id="{ item }">
      {{ item.id }}
    </template>

    <template v-slot:item.name="{ item }">
      {{ item.name }}
    </template>

    <template v-slot:item.price="{ item }">
      {{ item.price + " руб." }}
    </template>

    <template v-slot:item.actions="{ item }">
      <v-icon small class="mr-2" @click="editProduct(item.id)">
        mdi-pencil
      </v-icon>
      <v-icon small @click="deleteProduct(item.id)">
        mdi-delete
      </v-icon>
    </template>
  </v-data-table>
</template>

<script>
import CreateProductDialog from "../Dialogs/product/CreateProductDialog";

export default {
  name: "ProductsTable",
  inheritAttrs: false,
  data() {
    return {
      headers: [
        {
          text: "Номер",
          align: "start",
          sortable: false,
          value: "id"
        },
        {
          text: "Название товара",
          sortable: false,
          value: "name"
        },
        {
          text: "Цена",
          sortable: false,
          value: "price"
        },
        {
          text: "Действия",
          sortable: false,
          value: "actions"
        }
      ],
      filter: {
        page: 1,
        pageCount: 3,
        itemsPerPage: 20
      }
    };
  },
  methods: {
    updatePaginate(paginate) {
      this.$emit("updatePaginate", paginate);
    },
    createProduct(data) {
      this.$emit("createProduct", data);
    },
    editProduct(id) {
      this.$emit("toggleUpdateProduct", id);
    },
    deleteProduct() {}
  },
  components: {
    CreateProductDialog
  },
  props: {
    products: {
      type: Array,
      required: true
    },
    categories: {
      type: Array,
      required: true
    }
  }
};
</script>

<style scoped></style>
